.popup{
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0px 19px 0;
    background: rgba(0, 0, 0, 0.5);
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.5s, opacity 1s;
    /* display: none; */
    display: flex;
}

.popup__container{
    display: flex;
    flex-direction: column;
    padding: 0px 0px 39px 0px;
    position: relative;
}
@media screen and ( min-width: 1280px ){
    .popup__container{
        padding-top: 38px;
        
    }
}
.popup_opened{
    
    visibility: visible;
    opacity: 1;
}


.popup__form{
    background: #FFFFFF;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    min-height: 272px;
    min-width: 238px;
    padding: 25px 22px;
    display: flex;
    flex-direction: column;
}
