    margin-bottom: 15px;
    border: 0;
    padding: 0;
    background-image: url(data:image/png;base64,);
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0);
    align-self: flex-end;
    background-size: cover;
}
.popup__close:hover{
    opacity: 0.6;
    cursor: pointer;
}
@media screen and ( min-width: 1280px ){
    .popup__close{
        width: 32px;
        height: 32px;
        position: absolute;
        top: -2px;
        right: -40px;
    }
}
