.popup{
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 0px 19px 0;
    background: rgba(0, 0, 0, 0.5);
    box-sizing: border-box;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.5s, opacity 1s;
    /* display: none; */
    display: flex;
}
